import {LoadingController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FtpDirectoryBrowserViewModel } from "../../Model/ViewModel/FtpDirectoryBrowserViewModel";

/**
 * Generated class for the FtpDirectoryBrowserModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-ftp-directory-browser-add-edit-modal",
  templateUrl: "ftp-directory-browser-add-edit-modal.html"
})
export class FtpDirectoryBrowserAddEditModalPage {
  item: FtpDirectoryBrowserViewModel;
  mode: string;
  CanEditFtpFileBrowser: boolean;
  title:string;
   constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
  ) {

    this.item = new FtpDirectoryBrowserViewModel();
    this.item.remote_conn_id=navParams.data.filter.remote_conn_id;
    this.item.conn_type_key=navParams.data.filter.conn_type_key;
    this.item.encrypt_method_key=navParams.data.filter.encrypt_method_key;
    this.item.filepath = navParams.data.item.filepath;
    this.item.directory_name = navParams.data.item.directory_name;
    this.item.create_time = navParams.data.item.create_time;


    this.CanEditFtpFileBrowser = navParams.data.CanEditFtpFileBrowser;
    this.mode = navParams.data.mode;
    if(this.mode==="POST")
      this.title="新增資料夾";
    else
    this.title="變更資料夾";
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FtpDirectoryBrowserModalPage");
  }
}



